a {
  text-decoration: none;

  :hover {
    text-decoration: none;
  }
}

p {
  text-indent: 2em;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.footnotes {
  font-size: 0.9rem;
  opacity: 0.8;
}

.button {
  padding: 1rem;
  border-radius: 0.5rem;
  color: white;
  display: inline-block;
  margin: 0.5rem;

  &.brand1:hover {
    background-color: darken($brand1, 10%);
  }

  &.brand2:hover {
    background-color: darken($brand2, 10%);
  }

  &.brand3:hover {
    background-color: darken($brand3, 10%);
  }
}

.button-slim {
  padding: 0.6rem;
}

.brand1 {
  background-color: $brand1;
}

.brand2 {
  background-color: $brand2;
}

.brand3 {
  background-color: $brand3;
}

.button-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}